// -----------------------------------------------------------------------------
// This file contains all application-wide Sass mixins.
// -----------------------------------------------------------------------------

// BlockQuote Mixins //
@mixin blockquote($color) {
  border-color: $color;
  .blockquote-footer {
    color: $color;
  }
}

// generic transform //
@mixin transform($transforms) {
  -moz-transform: $transforms;
  -o-transform: $transforms;
  -ms-transform: $transforms;
  -webkit-transform: $transforms;
  transform: $transforms;
}

// rotate //
@mixin rotate ($deg) {
  @include transform(rotate(#{$deg}deg));
}

// scale //
@mixin scale($scale) {
  @include transform(scale($scale));
}
// translate //
@mixin translate ($x, $y) {
  @include transform(translate($x, $y));
}

// skew //
@mixin skew ($x, $y) {
  @include transform(skew(#{$x}deg, #{$y}deg));
}

// Badge outlines //
@mixin badge-variable-outlines($color) {
  color: $color;
  border: 1px solid $color;
}

// Beadcrumb variations //
@mixin breadcrumb-variations($color){
  background: lighten($color, 25%);
  border-color: $color;

  .breadcrumb-item {
    color: $color;

    &:before {
      color: inherit;
    }

    a {
      color: inherit;
    }
  }

  &.breadcrumb-custom {
    background: transparent;

    .breadcrumb-item {
      background: lighten($color, 25%);

      a {
        color: $color;
        border: none;

        &:before {
          border-left-color: lighten($color, 25%);
        }
      }

      span {
        color: $color;
      }

      &:last-child {
        background: transparent;
      }
    }
  }
}

// Social Media Buttons //
@mixin social-button($color) {
  background: $color;
  color: $white;

  &:hover,
  &:focus {
    background: darken($color, 10%);
    color: $white;
  }
  &.btn-social-icon-text {
    padding: 0 1.5rem 0 0;
    background: lighten($color, 10%);
    i {
      background: $color;
      padding: .75rem;
      display: inline-block;
      margin-right: 1.5rem;
    }
  }
}

// Social Media Outlined Buttons //
@mixin social-outline-button($color) {
  border: 1px solid $color;
  background: transparent;
  color: $color;
  &:hover {
    background: $color;
    color: $white;
  }
}

// Button Inverse Variations //
@mixin button-inverse-variant($color, $color-hover: $white) {
  background-color: rgba($color, 0.2);
  background-image: none;
  border-color: rgba($color, 0);
  &:not(.btn-inverse-light) {
    color: $color;
  }
  @include hover {
    color: $color-hover;
    background-color: $color;
    border-color: $color;
  }

  &.focus,
  &:focus {
    box-shadow: 0 0 0 3px rgba($color, .5);
  }

  &.disabled,
  &:disabled {
    color: $color;
    background-color: transparent;
  }

  &.active,
  &:active,
  .show > &.dropdown-toggle {
    color: $color-hover;
    background-color: $color;
    border-color: $color;
  }
}

// Card Inverse Variations //
@mixin card-inverse-variant($bg, $border, $color) {
  background: $bg;
  border: 1px solid $border;
  color: $color;
}

// Transition Duration //
@mixin transition-duration($duration) {
  -moz-transition-property:    $duration;
  -o-transition-property:      $duration;
  -webkit-transition-property: $duration;
  transition-property:         $duration;
}

// Pagination variations //
@mixin pagination-variants($color) {
  .page-item {
    &.active {
      .page-link {
        background: $color;
        border-color: $color;
      }
    }

    .page-link {
      &:hover {
        background: lighten($color,5%);
        border-color: $color;
        color: $white;
      }
    }
  }
}

// popover color variations //
@mixin popover-variant($color) {
  border-color: $color;
  .popover-header {
    background-color: map-get($theme-colors, $color);
    color: $white;
    border-bottom: 0;
  }
  &.bs-popover-top {
    .arrow {
      &:before,
      &:after {
        border-top-color: map-get($theme-colors, $color);
      }
    }
  }
  &.bs-popover-right {
    .arrow {
      &:before,
      &:after {
        border-right-color: map-get($theme-colors, $color);
      }
    }
  }
  &.bs-popover-bottom {
    .arrow {
      &:before,
      &:after {
        border-bottom-color: map-get($theme-colors, $color);
      }
    }
  }
  &.bs-popover-left {
    .arrow {
      &:before,
      &:after {
        border-left-color: map-get($theme-colors, $color);
      }
    }
  }
}

// Tooltip color  variations //
@mixin tooltip-variant($color) {
  .tooltip-inner {
    background:  map-get($theme-colors, $color);
    color: $white;
  }
  &.bs-tooltip-top {
    .arrow::before {
      border-top-color: map-get($theme-colors, $color);
    }
  }
  &.bs-tooltip-right {
    .arrow::before {
      border-right-color: map-get($theme-colors, $color);
    }
  }
  &.bs-tooltip-bottom {
    .arrow::before {
      border-bottom-color: map-get($theme-colors, $color);
    }
  }
  &.bs-tooltip-left {
    .arrow::before {
      border-left-color: map-get($theme-colors, $color);
    }
  }
}

// Ellipsis //
@mixin ellipsor {
  text-overflow: ellipsis;
  overflow: hidden;
  max-width: 100%;
  white-space: nowrap;
}
