/* poppins-300 - latin */
@font-face {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 300;
  src: url('../../dist/fonts/base/poppins-v6-latin-300.eot'); /* IE9 Compat Modes */
  src: local('Poppins Light'), local('Poppins-Light'),
  url('../../dist/fonts/base/poppins-v6-latin-300.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('../../dist/fonts/base/poppins-v6-latin-300.woff2') format('woff2'), /* Super Modern Browsers */
  url('../../dist/fonts/base/poppins-v6-latin-300.woff') format('woff'), /* Modern Browsers */
  url('../../dist/fonts/base/poppins-v6-latin-300.ttf') format('truetype'), /* Safari, Android, iOS */
  url('../../dist/fonts/base/poppins-v6-latin-300.svg#Poppins') format('svg'); /* Legacy iOS */
}
