// Import the Boss
@import
'../../node_modules/bootstrap/scss/bootstrap';

//$theme-flag: 1 !default; // DarkTheme = 2

// Color Palettes
$blue:              #1a88ff;
$indigo:            #7528f3;
$purple:            #7d55c7;
$pink:              #ea5198;
$red:               #e04958;
$orange:            #fd8b2c;
$yellow:            #ffc720;
$green:             #3eb058;
$teal:              #36cea1;
$cyan:              #2eabbf;
$black:             #000000;
$white:             #ffffff;
$gray:              #434a54;

$colors: (
        blue:             $blue,
        indigo:           $indigo,
        purple:           $purple,
        pink:             $pink,
        red:              $red,
        orange:           $orange,
        yellow:           $yellow,
        green:            $green,
        teal:             $teal,
        cyan:             $cyan,
        white:            $white,
        gray:             $gray,
        black:            $black
);


$theme-colors: (
        primary:         #4765ff,
        secondary:       #aca9bb,
        success:         #1ace1a,
        info:            #00aaff,
        warning:         #e0631a,
        danger:          #cd2145,
        light:           #f9fafb,
        dark:            #242a34
);

